@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Shadows+Into+Light+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Shadows+Into+Light+Two&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style-type: none;
  margin: 0;
}

li {
  display: inline;
}

li a {
  text-decoration: none;
  padding: 1rem;
}

/* switch toggle */
.theme-switch-wrapper {
  float: right;
}

.theme-switch {
  display: inline-block;
  height: 2rem;
  position: relative;
  width: 3.5rem;
}

.theme-switch input {
  display: none;
}

.slider {
  background-color: #1c1300;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
  border: 1px solid #1c1300;
}

.slider:before {
  background-color: #ded4b9;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
}

input:checked + .slider {
  background-color: #ded4b9;
}

input:checked + .slider:before {
  transform: translateX(1.5rem);
  background-color: #1c1300;
}

.slider.round {
  border-radius: 1rem;
}

.slider.round:before {
  border-radius: 50%;
}

/* header toggle */
.header {
  background-color: #ebc45e;
  padding: 1rem;
}

.header a {
  color: #1c1300;
  font-size: 20px;
  padding-right: 1.5rem;
}

.header-dark {
  background-color: #30230a;
  padding: 1rem;
}

.header-dark a {
  color: #ded4b9;
  font-size: 20px;
  padding-right: 1.5rem;
}

/*   COMMON STYLES   */
h1,
h2 {
  font-family: "Shadows Into Light Two", cursive;
}

.home-splash {
  position: relative;
  width: 100%;
  height: 95vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-splash::before {
  content: "";
  background-image: url(/eggs/static/media/erol-ahmed-leOh1CzRZVQ-unsplash.cdafed2c.jpg);
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.5;
}

.home-splash h1 {
  position: relative;
  text-align: center;
  letter-spacing: 1rem;
  font-size: 50px;
}

/*   LIGHT VS DARK   */

.home-splash .dark {
  color: rgba(255, 255, 255, 0.75);
}

.dark {
  color: #fff;
}

.home-what {
  width: 100%;
  height: 95vmin;
}

.home-why {
  width: 100%;
  height: 95vmin;
  background-color: #f2dca3;
}

.home-why-dark {
  width: 100%;
  height: 95vmin;
  background-color: #5b3c00;
  color: #fff;
}

.home-who {
  width: 100%;
  height: 95vmin;
}

